import React, { Component } from 'react';
import List from './List';
import Items from './Items';

const ITEMS_LOCAL_STORAGE_KEY = 'to-do-items';

class App extends Component {
  inputElement = React.createRef()
  constructor() {
    super();
  
    this.state = {
      items: this.getListFromLocalStorage(),
      currentItem: {
        text: '',
        key: '',
      },
    }
  }

  getListFromLocalStorage() {
    const data = localStorage.getItem(ITEMS_LOCAL_STORAGE_KEY);

    if (!data) {
      return [];
    }

    try {
      return JSON.parse(data) || [];
    } catch (e) {
      console.log(e);
      return [];
    }
  }

  putListToLocalShorage(items) {
    const data = JSON.stringify(items);

    localStorage.setItem(ITEMS_LOCAL_STORAGE_KEY, data);
  }

  updateItems(items) {
    this.putListToLocalShorage(items);
    this.setState({ items });
  }

  deleteItem = key => {
    const filteredItems = this.state.items.filter(item => {
      return item.key !== key
    })

    this.updateItems(filteredItems);
  }

  handleInput = e => {
    const itemText = e.target.value;
    const currentItem = { text: itemText, key: Date.now() };

    this.setState({
      currentItem,
    })
  }

  addItem = e => {
    e.preventDefault();
    const newItem = this.state.currentItem;

    if (newItem.text !== '') {
      const items = [...this.state.items, newItem];

      this.updateItems(items);
      this.setState({
        currentItem: { text: '', key: '' },
      })
    }
  }
  
  render() {
    return (
      <div className="App">
        <List
          addItem={this.addItem}
          inputElement={this.inputElement}
          handleInput={this.handleInput}
          currentItem={this.state.currentItem}
        />
        <Items entries={this.state.items} deleteItem={this.deleteItem} />
      </div>
    )
  }
}

export default App
