import React, { Component } from 'react';
import Favicon from 'react-favicon';
import logoIcon from '../assets/img/icon.png';
import cat from '../assets/img/cat.png';

class List extends Component {
  componentDidUpdate() {
    this.props.inputElement.current.focus()
  }

  renderIcon() {
    return (
      <div>
        <Favicon url={logoIcon} />
      </div>
    );
  }

  render() {
    return (
      <div className="add-task">
        {this.renderIcon()}
        <h2 className="add-task__header">To-do list</h2>
        <div className="add-task__container">
          <img className="add-task__img" src={cat} alt="cat" />
          <div className="add-task__text-container">
            <p className="add-task__text">&mdash; То, что тебе иногда не хватает мотивации, не значит, что ты плох в том, что делаешь. Это не значит, что пора сдаваться. Нормально иногда так себя чувствовать. Ты отлично справляешься, ты станешь ещё лучше. Ты сможешь.</p>
            <p className="add-task__text">&mdash; Напиши свой список дел! Мяу!</p>
          </div>
        </div>
        <div className="add-task__form-container">
          <form className="add-task__form" onSubmit={this.props.addItem}>
            <input
              className="add-task__input"
              placeholder="Задача на сегодня"
              ref={this.props.inputElement}
              value={this.props.currentItem.text}
              onChange={this.props.handleInput}
            />
            <button type="submit" className="add-task__button">Добавить</button>
          </form>
        </div>
      </div>
    )
  }
}

export default List
